







































































import Vue from 'vue';
import { TranslateResult } from 'vue-i18n';
import { makeQueryStringHandler } from '../../../server/components/usecases/utils/queryStringHandler';
import { StringKeyObject } from '../../../server/types/custom';
const queryStringHandler = makeQueryStringHandler;

export default Vue.extend({
  name: 'TeacherSignup',
  components: {},
  props: {},
  data() {
    return {};
  },
  computed: {
    stepData: {
      get(): { text: TranslateResult | string }[] {
        return [
          {
            text: 'Sign up for a teacher account now!Google',
          },
          {
            text: 'LINESkypeDiscordID',
          },
          {
            text: '1/1 appointment remaining',
          },
          {
            text: ' ',
          },
          {
            text: 'Find a teacher  1~2Sensei (: Aya Sensei',
          },
        ];
      },
    },
    routerData: {
      get(): StringKeyObject {
        const state = queryStringHandler.encodeQueryStringObj({
          state: {
            isTeacherApp: true,
          },
        });
        const query = queryStringHandler.parseQueryString(state);
        const routerData = { path: 'signup', query };
        return routerData;
      },
    },
  },
  mounted() {
    return;
  },
  methods: {},
});
